<template>
  <v-form class="col-6 mx-auto" v-slot="{ errors }" @submit="sendOrder">
    <div class="mb-3">
      <label class="form-label" for="email">Email :</label>
      <v-field
        type="email"
        name="Email"
        id="email"
        class="form-control"
        placeholder=" Email"
        :class="{ 'is-invalid': errors['Email'] }"
        rules="email|required"
        v-model="user.email"
      ></v-field>
      <ErrorMessage name="Email" class="invalid-feedback"></ErrorMessage>
    </div>
    <div class="mb-3">
      <label class="form-label" for="name"> :</label>
      <v-field
        type="text"
        name=""
        id="name"
        class="form-control"
        placeholder=" "
        :class="{ 'is-invalid': errors[''] }"
        rules="required"
        v-model="user.name"
      ></v-field>
      <ErrorMessage name="" class="invalid-feedback"></ErrorMessage>
    </div>
    <div class="mb-3">
      <label class="form-label" for="name"> :</label>
      <v-field
        type="text"
        name=""
        id="name"
        class="form-control"
        placeholder=" "
        :class="{ 'is-invalid': errors[''] }"
        :rules="isPhone"
        v-model="user.tel"
      ></v-field>
      <ErrorMessage name="" class="invalid-feedback"></ErrorMessage>
    </div>
    <div class="mb-3">
      <label class="form-label" for="name"> :</label>
      <v-field
        type="text"
        name=""
        id="name"
        class="form-control"
        placeholder=" "
        :class="{ 'is-invalid': errors[''] }"
        rules="required"
        v-model="user.address"
      ></v-field>
      <ErrorMessage name="" class="invalid-feedback"></ErrorMessage>
    </div>
    <div class="mb-3">
      <label class="form-label" for="message"> :</label>
      <textarea
        class="form-control"
        name="message"
        v-model="message"
        id="message"
        rows="3"
      ></textarea>
    </div>
    <div class="d-grid">
      <button type="submit" class="btn btn-danger"></button>
    </div>
  </v-form>
</template>

<script>
import { defineRule, Form, Field, ErrorMessage, configure } from 'vee-validate'
import { required, email, min, max } from '@vee-validate/rules'
import { localize, loadLocaleFromURL } from '@vee-validate/i18n'

defineRule('required', required)
defineRule('email', email)
defineRule('min', min)
defineRule('max', max)

loadLocaleFromURL(
  'https://unpkg.com/@vee-validate/i18n@4.1.0/dist/locale/zh_TW.json'
)
configure({
  // 
  generateMessage: localize('zh_TW') //  locale
})

export default {
  components: {
    VForm: Form,
    VField: Field,
    ErrorMessage: ErrorMessage
  },
  data () {
    return {
      user: {},
      message: ''
    }
  },
  methods: {
    sendOrder (value, { resetForm }) {
      console.log(value)
      const user = this.user
      const message = this.message
      this.$http
        .post(
          `${process.env.VUE_APP_API}/v2/api/${process.env.VUE_APP_PATH}/order`,
          { data: { user }, message }
        )
        .then((res) => {
          alert('')
          resetForm()
          this.$store.dispatch('getCart')
          this.user = {}
        })
        .catch((err) => {
          console.log(err)
        })
    },
    isPhone (value) {
      const phoneNumber = /^(09)[0-9]{8}$/
      return phoneNumber.test(value) ? true : '(09XX-XXX-XXX)'
    }
  }
}
</script>
